@import url(https://fonts.googleapis.com/css2?family=New+Tegomin&display=swap);
.wrapper{
    height: 100%;
}
.heroes {
  padding: 5px;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s;
}

.heroes:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}

.current-hero {
  color: white;
  display: -webkit-flex;
  display: flex;
  margin: 0 auto;
}
.heroImg {
  width: 100px;
  height: 100px;
  border-radius: 40%;
}

.hero-info {
  width: 300px;
  font-weight: bold;
  text-align: center;
  border: 1px solid;
  border-radius: 20px;
  padding: 10px;
}

.description {
  width: 98%;
  max-height: 100%;
  height: 250px;
  min-height: 0;
  color: #dddddd;
  font-weight: lighter;
  line-height: 122%;
  overflow-y: scroll;
  padding: 8px;
  margin: 0 auto;
  border-radius: 5%;
}
.description::-webkit-scrollbar {
  width: 10px;
  border: 1px solid;
  border-radius: 10px;
}
.description::-webkit-scrollbar-thumb {
  background-color: lightslategray;
  border: 2px solid;
  border-radius: 10px;
}

.created-by {
  margin-top: 20px;
}

.spells-wrapper {
  -webkit-justify-content: center;
          justify-content: center;
